.footer {
   background-color: $color-secondary;
   grid-column: full-start / full-end;
   padding: 8rem;
}

.nav {
   list-style: none;

   display: grid;
   grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
   gap: 2rem;
   align-items: center;

   &__link:link,
   &__link:visited {
      font-size: 1.4rem;
      color: #fff;
      text-decoration: none;
      font-family: $font-display;
      text-transform: uppercase;
      text-align: center;
      padding: 1.5rem;
      display: block;
      transition: all 0.2s;
   }

   &__link:hover,
   &__link:active {
      background-color: rgba(#fff, 0.05);
      transform: translateY(-3px);
   }
}

.copyright {
   font-size: 1.4rem;
   color: $color-grey-light-2;
   margin-top: 6rem;
   margin-left: auto;
   margin-right: auto;
   text-align: center;
   width: 80%;
}
